/* 
  2018/09/10  - 新加VIP页面
  2018/09/11  - 预留进入是加载数据方式
              - 字体图标vip图标p显示
  2018/09/12  - 添加获取vip信息
  2018/09/13  - 微信用户信息放全局
              - 添进页面关闭组件
  2018/09/14  - 添加提示，接口调公共组件
  2018/09/26  - 添加邀请好友列表
 */
<template>
  <div v-wechat-title="title">
    <div v-infinite-scroll="loadMore(friend.loading)" infinite-scroll-disabled="friend.loading" infinite-scroll-distance="10">
      <div class="bg-ff margin-t-30">
        <VIPNav class='padding-all-30'/>
      </div>
      <div class='margin-t-30 padding-all-30 bg-ff color-2d bg-flower' :style='{backgroundImage: `url(${bgImg})`}'>
        <div class='f-34'>
          <div class='display-inlineblock margin-r-20'>VIP会员</div>
          <div class='display-inlineblock bg-2d color-ff f-24 border-radius-5 useBlock'>使用中</div>
        </div>
        <div class='margin-t-20 f-30' v-if='userInfo.vipExpireDate'>将于{{userInfo.vipExpireDate|formatYMD}}到期</div>
      </div>
      <div class="margin-t-30 bg-ff">
        <PriceList></PriceList>
      </div>
      <div class="margin-t-30 bg-ff">
        <QRFriends :showfriendnum='true'></QRFriends>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  name: "VIPDetails",
  data() {
    return {
      title: "我的VIP",
      vipImg: `${reqCfg.webUrl}static/img/vip.png`,
      // 当前页
      CurrentPage: false,
      // 背景图
      bgImg: `${reqCfg.webUrl}static/img/image_vip_using@3x.png`
    };
  },
  computed: {
    ...mapGetters(["userInfo", "friend"])
  }
};
</script>
<style scoped>
.bg-flower {
  background: url() no-repeat center center;
  background-size: 100% 100%;
}
.useBlock {
  width: 1rem;
  height: 0.38rem;
  line-height: 0.4rem;
}
</style>



