import $ from "@/store/function";
const share = {
  state: {
    reqCfg,
    shareCfg,
    qrCfg
  },
  mutations: {
    shareConfig(state, val) {
      const json = { url: encodeURIComponent(location.href.split("#")[0]) };
      $.ajax("get", "WeiXin/getWXConfig", json, res => {
        wx.config({
          debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
          appId: state.reqCfg.appId, // 必填，公众号的唯一标识
          timestamp: res.data.timestamp, // 必填，生成签名的时间戳
          nonceStr: res.data.nonceStr, // 必填，生成签名的随机串
          signature: res.data.signature, // 必填，签名
          jsApiList: [
            "onMenuShareTimeline", //分享到朋友圈 1.0
            "onMenuShareAppMessage", //分享给朋友 1.0
            "onMenuShareQQ", //分享到QQ 1.0
            "onMenuShareQZone", //分享到QQ空间 1.0
            "updateTimelineShareData", //分享给朋友/QQ 1.4
            "updateAppMessageShareData", //分享到朋友圈/QQ空间 1.4
            "onMenuShareWeibo" //分享到腾讯微博
          ] // 必填，需要使用的JS接口列表
        });
        val.callback && val.callback();
      });
    },
    userShare(state, val) {
      const shareData = {
        title: val.title, // 分享标题
        desc: val.desc, // 分享描述
        link: val.link, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
        imgUrl: val.imgUrl, // 分享图标
        success(res) {
          val.callback && val.callback(res);
        },
        cancel(res) {
          val.errcallback && val.errcallback(res);
        }
      };
      wx.ready(() => {
        //需在用户可能点击分享按钮前就先调用
        if (wx.onMenuShareAppMessage) {
          wx.onMenuShareTimeline(shareData);
          wx.onMenuShareAppMessage(shareData);
          wx.onMenuShareQQ(shareData);
          wx.onMenuShareQZone(shareData);
        } else {
          wx.updateTimelineShareData(shareData);
          wx.updateAppMessageShareData(shareData);
        }
        wx.onMenuShareWeibo(shareData);
      });
    }
  }
};

export default share;



