import Vue from "vue";
import { Toast, Indicator } from "mint-ui";
/*
  页面的跳转-添加：
    - this.pushPage('Home',{name:'参数'})
  页面的跳转-替换：
    - this.pushPage('Home',{name:'参数'})
  页面的跳转-返回：
    - this.back(-5) 默认：-1
  回到顶部：
    - this.scrollTop()
  提示：
    - this.Toast('提示',2000,null,'middle')
  加载：
    - this.showIndicator('加载中...','fading-circle')
  关闭加载：
    - this.closeIndicator()
*/
// 页面的跳转-添加
Vue.prototype.pushPage = function(val, parameter = null) {
  this.$router.push({
    path: `/${val}`,
    query: parameter
  });
};
// 页面的跳转-替换
Vue.prototype.replacePage = function(val, parameter = null) {
  this.$router.replace({
    path: `/${val}`,
    query: parameter
  });
};
// 页面的跳转-返回
Vue.prototype.back = function(val = -1) {
  this.$router.go(val);
};
// 回到顶部
Vue.prototype.scrollTop = function() {
  document.documentElement.scrollTop = 0; //回顶部
  document.body.scrollTop = 0; //回顶部
};
// 提示
Vue.prototype.Toast = function(message, duration = 2000, iconClass, position = "middle") {
  Toast({
    message,
    position,
    iconClass,
    duration
  });
};
// 加载
Vue.prototype.showIndicator = function(text = "加载中...", spinnerType = "fading-circle") {
  Indicator.open({
    text,
    spinnerType
  });
};
// 关闭加载
Vue.prototype.closeIndicator = function() {
  Indicator.close();
};
// 加载更多好友
Vue.prototype.loadMore = function(load) {
  if (this.$store.getters.friend.count && !load) {
    this.showIndicator();
    this.$store.commit("stopGetFriend");
    setTimeout(() => {
      this.$store.commit("getShareDetailsManager", {
        callback: () => {
          this.closeIndicator();
        }
      });
    }, 100);
  }
};



